export default {
  fetch() {
    return fetch(`http://${process.env.API_SERVER_HOST}:${process.env.API_SERVER_PORT}/time`).then(res => {
      return res.json()
    }).then(data => {
      console.log(data)
      this.currentTime = data
    })
  },
  data() {
    return {
      currentTime: {},
      a: process.env.API_SERVER_HOST,
      b: process.env.API_SERVER_PORT
    }
  }
}
